.header--carrinho{
    width: 100vw;
}
.container--products{
    position: absolute;
    top: 40%;
    left: 40%;
    text-align: center;
}
main .container--icon{
    margin-bottom: 1rem;
}
.header--carrinho .container--shopCart {
    display: none;
}

.container--purshase--car{
    text-align: center;
}
.container--purshase--car button{
    width: 300px;
    height: 100px;
    padding: 1rem;
    margin: 1rem 0;
    border: none;
    border-radius: 1rem;
    background: transparent;
}
.container--purshase--car .button--purshase{   
    font-size: 1rem;
    font-weight: bold;
    letter-spacing: 2px;
    width: 100%;
    padding: .8rem;
    color: #fff;
    text-align: center;
    text-decoration: none;
    background: #f3123c;
    border: none;
    border-radius: .5rem;
    cursor: pointer;
    transition: 1s;
}

.container--purshase--car .button--purshase:hover {
    border-radius: 1rem;
    box-shadow: 0 0 5px #f3123c,
                0 0 20px #f3123bd3;
}